.two-columns {
  margin: 0px;
  padding: 0px;
  background-color: white;
  margin: 3em;
  padding: 2em;
  border: 1px solid black;

  .column-container {
    display: flex;
    justify-content: space-between;
  }

  h2,
  h3,
  h6,
  p,
  textarea {
    font-family: sans-serif;
  }

  h2 {
    font-size: 4em;
  }

  h3 {
    font-size: 3em;
  }

  h6 {
    font-size: 2em;
    margin-top: 0;
  }

  p,
  textarea {
    font-size: 2em;
  }

  textarea {
    width: 45%;
    padding: 0.5em;
  }

  hr {
    border: none;
    height: 0.2em;
    background-color: black;
  }
}
